import React, {useState, useEffect} from 'react';
import './styles.css';

import Navbar from '../navbar/Navbar';
import BurguerButtom from '../../../components/burgueBottom/BurguerButton';
import Button from '../../../ui/button/Button';
import SocialMedia from '../../../ui/social-media/SocialMedia';

const Header = () => {

  /** Initial value display width */
  const [width, setWidth] = useState(window.innerWidth);
  const [isMobile, setIsMobile] = useState(width < 720);
  const [desktop, setDesktop] = useState(width > 720);

  useEffect(() => {
    /** Change value of type display */
    const changeDisplay = () => {
      window.addEventListener('resize', () => {
        setWidth(window.innerWidth);
      });
      setIsMobile(width < 720);
      setDesktop(width > 720);
    };

    changeDisplay();
  });
  
  return (
    <header className="header" id="home">
      {/** type navbar */}
      {isMobile && <BurguerButtom />}
      {desktop && <Navbar />}
      <div className="container">
        <section className="header-content">
          <h1 className="title-header">
            Mike_ <br />
            Front-End <br />
            Developer
          </h1>
          <p className="description-header">Code is my life!</p>
          <div className="social"><SocialMedia /></div>
          <br />
          <Button
            href="#portfolio"
            type="large"
            color="orange"
            text="proyectos"
            outline="outline"
          />
        </section>
        <span className="text-water">DEVELOPER</span>{/** Marca de agua*/}
      </div>
    </header>
  );
};

export default Header;
